    transform: translateY(-10px);
    opacity: 0; }
  to {
    transform: translateY(0px);
    opacity: 1; } }

@keyframes tsi-slideRight {
  from {
    transform: translateX(-10px);
    opacity: 0; }
  to {
    transform: translateY(0px);
    opacity: 1; } }

